import Link from '../../UI/Link/Link'

import appImg from '../../img/app.png'
import starImg from '../../img/star.png'
import valletImg from '../../img/vallet.png'
import hostelImg from '../../img/hostel0.png'
import bookImg from '../../img/open-book.png'
import calendarImg from '../../img/calendar.png'
import listImg from '../../img/list.png'
import globeImg from '../../img/globe.png'
import searchImg from '../../img/search.png'

import {motion} from 'framer-motion'

import './MainPage.css'

const usefulLinks = [
    {img: appImg, text: 'Версия ЛК для Android вер.1.3.0'},
    {img: starImg, text: 'Рейтинг групп'},
    {img: valletImg, text: 'Выплаты'},
    {img: hostelImg, text: 'Общежитие'},
]

const studyLinks = [
    {img: bookImg, text: 'Библиотека'},
    {img: calendarImg, text: 'Расписание занятий'},
    {img: listImg, text: 'Информация по образовательным программам'},
    {img: globeImg, text: 'СДО Дистанционное образование'},
    {img: searchImg, text: 'Система тестирования'},
]

export default function MainPage() {

    const renderUsefulLinks = () => {
        return usefulLinks.map((item, index) => {
            return(
                <Link 
                    key={index}
                    img={item.img}
                    text={item.text}
                />
            )
        })
    }

    const renderStudyLinks = () => {
        return studyLinks.map((item, index) => {
            return(
                <Link 
                    key={index}
                    img={item.img}
                    text={item.text}
                />
            )
        })
    }

    return(
        <div className="MainPage">
            <h2>Главная</h2>
            
            <motion.div
                initial={{ opacity: 0,  scale: .7 }}
                animate={{ opacity: 1,  scale: 1 }}
                transition={{duration: .5}}
            >
                <div className="linksBlock">
                    <h3>Полезные ссылки:</h3>

                    <div className='links'>
                        { renderUsefulLinks() }
                    </div>
                </div>

                <div className="linksBlock">
                    <h3>Образовательные ресурсы:</h3>

                    <div className='links'>
                        { renderStudyLinks() }
                    </div>
                </div>

                <div className="linksBlock">
                    <h3>Обратная связь</h3>

                    <div className='links'>
                        <p className='review'>Отзывы, замечания и предложения отправляйте на почту adres@dddd.net c указанием темы личный кабинет студента.</p>
                    </div>
                </div>
            </motion.div>
        </div>
    )
}