import { AnimatePresence, LayoutGroup, motion } from "framer-motion";
import React from "react";
import Accordion from "../../UI/Accordion/Accordion";
import './News.css'

const news = [
    { 
        date: "11.05.2022",
        head: "Оценка качества образовательного процесса в УГНТУ глазами студента",
        text: `Для оценки качества образовательного процесса просим ответить на вопросы анкеты 'Качество образовательного процесса глазами студента'. Опрос анонимный, результаты опроса будут использоваться только в обобщенном виде.Для оценки качества образовательного процесса просим ответить на вопросы анкеты 'Качество образовательного процесса глазами студента'. Опрос анонимный, результаты опроса будут использоваться только в обобщенном виде.`
    },
    { 
        date: "23.12.2021",
        head: "В университете доступна годовая лицензия MATLAB для сотрудников и студентов",
        text: "Уважаемые сотрудники! В университете доступна годовая лицензия MATLAB для сотрудников и студентов. Для установки лицензии MATLAB в компьютерный класс/лабораторию обратитесь в ИАУ."
    },
    { 
        date: "30.04.2019",
        head: "Оценка качества образовательного процесса в УГНТУ глазами студента",
        text: `Для оценки качества образовательного процесса просим ответить на вопросы анкеты 'Качество образовательного процесса глазами студента'. Опрос анонимный, результаты опроса будут использоваться только в обобщенном виде.Для оценки качества образовательного процесса просим ответить на вопросы анкеты 'Качество образовательного процесса глазами студента'. Опрос анонимный, результаты опроса будут использоваться только в обобщенном виде.`
    },
    { 
        date: "01.08.2018",
        head: "В университете доступна годовая лицензия MATLAB для сотрудников и студентов",
        text: "Уважаемые сотрудники! В университете доступна годовая лицензия MATLAB для сотрудников и студентов. Для установки лицензии MATLAB в компьютерный класс/лабораторию обратитесь в ИАУ."
    },
]

export default function News(){
    const renderNews = () => {
        return news.map((newsItem, index) => {
            return(
                <Accordion
                    key={index}
                    date={newsItem.date}
                    head={newsItem.head}
                >
                    {newsItem.text}
                </Accordion>
            )
        })
    }

    return(
        <div className="News">
            <h2>Новости</h2>

            <motion.div
                initial={{ opacity: 0,  scale: .7 }}
                animate={{ opacity: 1,  scale: 1 }}
                transition={{duration: .5}}
            >
                { renderNews() }
            </motion.div>
        </div>
    )
}